:root {
  --white: #FFFFFF;
  --darkblue: #1D242D;
  --blue: #3D4C5E;
  --lightgray: #F9FbFc;
  --sectiongray: #EAEEF2;
  --gray: #929CAB;
  --darkgray: #404D5C;
  --browngray: #868686;
  --red: #F92626;
  --active: #0CA4EA;
}

* {
  padding: 0;
  margin: 0;
}

body {
  overflow-x: hidden;
  font-family: 'Gothic A1', sans-serif;
  color: #1D242D;
  color: var(--darkblue);
  font-weight: 400;
  font-size: 18px;
  -webkit-font-smoothing: antialiased;
}


#main {
  max-width: 1560px;
  width: 90vw;
  margin: 0 auto;
  min-height: calc(100vh - 88px - 285px);
}

ul, li {
  list-style: none;
  text-decoration: none;
}

a {
  color: inherit;
  text-decoration: none;
}

a.link {
  color: #1D242D;
  color: var(--darkblue);
  font-size: 18px;
  font-weight: 700;
}

a.alt-link {
  color: #929CAB;
  color: var(--gray);
  font-size: 16px;
  font-weight: 600;
}

a.link:hover, a.alt-link:hover {
  text-decoration: underline;
  /*border-bottom: 1px solid var(--darkblue);*/
}

a.link > img {
  height: 30px;
}

p.link {
  cursor: pointer;
}

a.link .sticker {
  width: 20px;
  height: 20px;
  border-radius: 20px;
  background: #F92626;
  background: var(--red);
  color: #FFFFFF;
  color: var(--white);
  text-align: center;
  font-size: 15px;
  line-height: 22px;
  vertical-align: middle;
  position: absolute;
  bottom: -7px;
  left: -7px;
}

a.button {
  background: #FFFFFF;
  background: var(--white);
  text-align: center;
  display: block;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 15px 22px 13px;
  border-radius: 8px;
  color: #1D242D;
  color: var(--darkblue);
  font-size: 18px;
  font-weight: 600;
}

a.button.main {
  background: #0CA4EA;
  background: var(--active);
  color: #FFFFFF;
  color: var(--white);
}

a.button.warning {
  background: #F92626;
  background: var(--red);
  color: #FFFFFF;
  color: var(--white);
}

.red {
  color: #F92626;
  color: var(--red);
}

h1 {
  font-weight: 700;
  font-size: 78px;
}

h2 {
  font-weight: 700;
  font-size: 29px;
}

h3 {
  font-weight: 700;
  font-size: 24px;
}

h4 {
  font-weight: 700;
  font-size: 18px;
}

h5 {
  font-weight: 700;
  font-size: 21px;
  text-transform: uppercase;
  color: #404D5C;
  color: var(--darkgray);
}

h1.light, h2.light, h3.light, h4.light {
  color: #FFFFFF;
  color: var(--white);
} 

/*.loading {
  color: transparent;
  background: var(--sectiongray);
}*/

h1, h2, h3, h4, h5, h6, p {
  -webkit-animation: loading 0.2s steps(1);
          animation: loading 0.2s steps(1);
}

@-webkit-keyframes loading {
  0% {
    color: transparent;
    background: #000000;
    opacity: 0.07;
  }
  100% {
    color: auto;
    background: auto;
    opacity: 1;
  }
}

@keyframes loading {
  0% {
    color: transparent;
    background: #000000;
    opacity: 0.07;
  }
  100% {
    color: auto;
    background: auto;
    opacity: 1;
  }
}


.split-row {
  grid-gap: 10px;
  gap: 10px;
  display: flex !important;
  justify-content: space-between;
  flex-wrap: wrap;
}


.list-row {
  margin-top: 50px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.row {
  display: flex;
  flex-wrap: wrap;
}

.db-3 {
  flex-basis: 25%;
}

.db-3.gap-10 {
  /* margin: 0 auto; */
  flex-basis: calc(25% - 10px);
}

.db-3.gap-20 {
  /* margin: 0 auto; */
  flex-basis: calc(25% - 20px);
}

.db-4 {
  flex-basis: 33.333333%;
}

.db-4.gap-10 {
  /* margin: 0 auto; */
  flex-basis: calc(33.333333% - 10px);
}

.db-4.gap-20 {
  /* margin: 0 auto; */
  flex-basis: calc(33.333333% - 20px);
}

.db-6 {
  flex-basis: 50%;
}

.db-6.gap-40 {
  /*margin: 0 auto;*/
  flex-basis: calc(50% - 40px);
}

.db-12 {
  flex-basis: 100%;
}

hr {
  margin: 20px 0;
  border-color: #EAEEF2;
  border-color: var(--sectiongray);
}

.split-above {
  margin-top: 140px;
}

.square-img {
  height: 18px;
  height: 18px;
}


.card {
  flex: 1 1;
  border-radius: 15px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.card > img,
.list-item > img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}

.list-item.shadow.icon {
  display: flex;
  justify-content: center;
  align-items: center;
}

.list-item.shadow.icon > img {
  width: 70%;
}

.card.full-image {
  position: relative;
}

.card.full-image > img {
  position: absolute;
  z-index: 0;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  object-fit: cover;
  width: 100%;
  height: 100%;
  border-radius: 15px;
}

.button {
  z-index: 2;
}

.card > img {
  width: 90%;
  height: 90%;
  transition: all 0.2s ease;
}
.card:hover > img {
    width: 100%;
    height: 100%;
}

.card.shadow {
  background: #EAEEF2;
  background: var(--sectiongray);
}

.card-extra-small {
  height: 150px;
}

.card-small {
  height: 300px;
}

.card-large {
  height: 500px;
}

.product-card {
  display: flex;
  justify-content: left;
  height: 140px;
  vertical-align: middle;
  position: relative;
}

.product-card .product-image {
  height: 90%;
  background: #EAEEF2;
  background: var(--sectiongray);
  margin: 7px;
}
.product-card .product-image img {
  height: 100%;
}

.product-content {
  margin: 7px;
  height: 90%;
}

.product-content h3 {
  padding-top: 20px;
}

.product-content p {
  padding-top: 5px;
}

.w-30 {
  min-width: 30%;
}

.w-100 {
  width: 100%;
}

.g-5 {
  grid-gap: 5px;
  gap: 5px;
}

.g-10 {
  grid-gap: 10px;
  gap: 10px;
}

.g-20 {
  grid-gap: 20px;
  gap: 20px;
}

.jc-left {
  justify-content: left;
}

.jc-center {
  justify-content: center;
}

.jc-spaceb {
  justify-content: space-between;
}

.jc-spacea {
  justify-content: space-around;
}

.ai-center {
  display: flex;
  align-items: center;
  position: relative;
}

.m-0 {
  margin: 0 !important;
}

.mt-40 {
  margin-top: 40px !important;
}

.mt-20 {
  margin-top: 20px !important;
}


.mb-60 {
  margin-bottom: 60px !important;
}

.mb-40 {
  margin-bottom: 40px !important;
}

.mb-20 {
  margin-bottom: 20px !important;
}

.mb-10 {
  margin-bottom: 10px !important;
}


.m0-10 {
  margin: 0 10px !important;
}

.p-10 {
  padding: 10px !important;
}

#header {
    max-width: 1560px;
    width: 90vw;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    height: 88px;
    align-items: center;
}

.links > .link {
    margin: 0 30px;
}


.burger {
    display: none;
    height: 20px;
    width: 30px;
    position: relative;
    cursor: pointer;
  }
  
  .burger-bar {
    height: 4px;
    border-radius: 2px;
    width: 30px;
    position: absolute;
    left: 0;
    background: var(--darkblue);
  }
  
  .burger-bar-1 {
    top: 0px;
  }
  
  .burger-bar-2 {
    top: 8px;
  }
  
  .burger-bar-3 {
    top: 16px;
  }
  
  @media(max-width: 1060px){
    .burger {
      display: block;
    }
    .burger.open .burger-bar-1 {
        top: 10px;
        -webkit-transform: translateY(-50%) rotate(45deg);
                transform: translateY(-50%) rotate(45deg);
    }
    .burger.open .burger-bar-2 {
        display: none;
    }
    .burger.open .burger-bar-3 {
        top: 10px;
        -webkit-transform: translateY(-50%) rotate(-45deg);
                transform: translateY(-50%) rotate(-45deg);
    }
    .nav {
      display: none;
      position: absolute;
      top: 87px;
      left: 0;
      right: 0;
      padding: 10px 5vw 40px;
      z-index: 10;
      background: var(--white);
      box-shadow: 0 4px 6px rgba(12, 14, 16, 0.2);
    }
    .nav a {
      display: block;
      padding: 8px 0;
      margin: 15px 0;
    }
    .nav.active {
      display: block;
    }
    .cart-text {
      display: none;
    }
  }
